import { useState, useEffect } from "react";
import * as $ from 'jquery';

const Contact = () => {
  const [status, setStatus] = useState('Submit');
  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus("Sending...");
    const { name, email, message } = e.target.elements;
    let details = {
      name: name.value,
      email: email.value,
      message: message.value,
    };

    let response = await fetch("http://localhost:5000/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(details),
    });
    setStatus("Submit");
    let result = await response.json();
    alert(result.status);
  };
/*
  useEffect(() => {
    $('.collapse').collapse();
  }, [status]);
*/
  return (
    <div className="contact px-5">
      <h2 className="prods-title text-center mb-5 pt-4">
        <b>Contacto</b>
        <hr className="h-line bar" />
      </h2>

      <div className="form-div">
        <form onSubmit={handleSubmit}>
          {/*
          <div>
            <label htmlFor="name">Name:</label>
            <input type="text" id="name" required />
          </div>
          <div>
            <label htmlFor="email">Email:</label>
            <input type="email" id="email" required />
          </div>
          <div>
            <label htmlFor="message">Message:</label>
            <textarea id="message" required />
          </div>
          <button type="submit">{status}</button>
        </form>*/}
          <div className="name form-group">
            {/*<label for="userName">Your name</label>*/}
            <input type="name" className="form-control" id="name" aria-describedby="nameHelp" placeholder="Tu nombre" />
          </div>
          <div className="email form-group">
            {/*<label for="userEmail">Email address</label>*/}
            <input type="email" className="form-control" id="email" aria-describedby="emailHelp" placeholder="Tu correo" />
            <small id="emailHelp" className="form-text text-muted">Debe ser un correo vlido.</small>
            <div className="custom-control custom-switch">
              <input type="checkbox" className="custom-control-input" id="customSwitch1" />
              <label className="custom-control-label" for="customSwitch1">Urgente?</label>
            </div>
          </div>
          <div className="textarea form-group">
            {/*<label for="exampleFormControlTextarea1">Example textarea</label>*/}
            <textarea className="form-control text-area" id="message" rows="3" placeholder="Deja tu mensaje"></textarea>
          </div>
          <button type="submit" className="btn btn-primary">{status}</button>
        </form>
      </div>
    </div>
  );
};

export default Contact;
